body{
  font-family: 'Montserrat', sans-serif;
}

/*Div Container For The Entire App*/
#content{
  width: 360px;
  height: 640px;
  display: grid;
  background-color: beige;
  grid-template-columns: 1fr;
  grid-template-rows: 100px 25px 25px 390px 80px;
  grid-row-gap: 5px;
  row-gap: 5px;
}

/*Div Container For The Buttons at the Bottom of the Screen (Filter and Delete Buttons)*/
#tools {
  width:360px;
  margin:0;
  grid-row: 5 / span 1;
  grid-column: 1 / span 1;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
}

#column-labels {
  grid-row: 3 /span 1;
  grid-column: 1 /span 1;
  display: flex;
  justify-content: space-between;
  margin-left: 15px;
  margin-right: 15px;
  font-weight: bold;
}

h1{
  width:360px;
  height: 100px;
  line-height: 100px;
  grid-row: 1 /span 1;
  grid-column: 1/span 1;
  text-align: center;
  margin-top: 0;
  font-size: 40px;
}

.accent {
  background-color: #C5AA8C;
}

.material-icons-outlined.md-38 {
  font-size: 3em;
}

/* For Add Button */

#item_list {
    width:360px;
    max-height: 330px;
    grid-row: 4 / span 1;
    grid-column: 1 / span 1;
    margin:0;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    overflow-y: auto;
}

#no-items{
    padding-left: 10pt;
    padding-right: 10pt;
    text-align: center;
    font-size: 20px;
}

#add{
    /*margin: 10px 30px 10px 30px;*/
    margin: 10px 30px 10px 30px;
    height: 40px;
    width: 300px;
    line-height: 40px;
    background-color: #664C43;
    color: beige;
    font-size: 40px;
    text-align: center;
    display: inline-block;
    font-family: 'Montserrat', sans-serif;
    border: none;
    border-radius: 5px;
}
.item-name {
    flex-grow: 5;
    flex-basis: 93%;
    resize: none;
    width: 85%;
    min-height: 25px;
    height: auto;
    border: none;
    background-color: beige;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
}

.item-name:focus {
    outline: none !important;
    width: 85%;
    height: 25px;
    border: none;
    background-color: beige;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    border-bottom: 2px solid #664C43;
}


.task-item-uncompleted{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 5px 10px;
    grid-column-gap: 8px;
    -webkit-column-gap: 8px;
            column-gap: 8px;
}

.task-item-completed{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin: 5px 10px;
    grid-column-gap: 8px;
    -webkit-column-gap: 8px;
            column-gap: 8px;
    text-decoration: line-through;
}

.check-complete {
    flex-grow: 1;
    flex-basis: 7%;
    width: 1.3em;
    height: 1.3em;
}


