@import url('https://fonts.googleapis.com/icon?family=Material+Icons+Outlined');

#delete-dropup{
    height: 80px;
    width: 100%;
    line-height: 80px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: #664C43;
    border-style: solid;
}

#filter-dropup{
    height: 80px;
    width: 100%;
    line-height: 80px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: #664C43;
    border-style: solid;
}

.dropup {
    position: relative;
    display: inline-block;
    /*display: none;*/
    flex-grow: 1.1;
    flex-basis: 0;
}

.dropup-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: #ebe3ca;
    min-width: 160px;
    bottom: 80px;
    z-index: 1;
    border: #664C43;
}

.dropup-content button {
    border-top: 2px solid #664C43;
    border-left: 2px solid #664C43;
    border-right: 2px solid #664C43;
    border-bottom: 0px;
    border-style: solid;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    padding: 5px 16px;
    text-decoration: none;
    display: block;
    background-color: #ebe3ca;
}

.accent {
    background-color: #C5AA8C;
}