.dropdown {
    float: right;
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: flex;
    flex-direction: column;
    position: fixed;
    background-color: #f1f1f1;
    min-width: 88px;
    overflow: auto;
    margin-right: 5px;
    left: 227px;
    z-index: 1;
}

.dropdown-content button {
    border-top: 2px solid #664C43;
    border-left: 2px solid #664C43;
    border-right: 2px solid #664C43;
    border-bottom: 0px;
    border-style: solid;
    font-size: 12px;
    font-family: 'Montserrat', sans-serif;
    padding: 4px 8px;
    text-decoration: none;
    display: block;
    background-color: #ebe3ca;
}
/*Add outline to the priority button currently pressed*/
#priority-outline {
    box-shadow: 0px 0px 0px 2px #664C43;
}

#high-option{
    border-bottom: 2px solid #664C43;
}
